$().ready(function(){
    $(".dog_foot_tit li").mouseover(function(){
        var _index = $(this).index();
        $(".bd1body2>ul").eq(_index).show().siblings().hide();
    $(this).addClass("bd1headli").siblings().removeClass("bd1headli");
    });
})

